export default {
  props: {
    status: {
      required: true,
      type: String
    }
  },
  data: function data() {
    return {
      colorMap: {
        queued: '#17a2b8',
        seeking: '#007bff',
        converting: '#007bff',
        'has-failures': '#ffc107',
        failed: '#ff4402',
        finished: '#28a745'
      }
    };
  },
  computed: {
    shouldPulse: function shouldPulse() {
      return ['queued', 'seeking', 'converting'].includes(this.status);
    }
  }
};